@import "variables";

/* web fonts */

@import url(http://fonts.googleapis.com/css?family=Lato:300,400,700);

/* main styles */

body {
    background-color: $main-bg-color;
    background-color: $medium-bg-color;
    font-family: Lato, sans-serif;
    color: $main-text-color;
}

a {
    color: $content-link;
}

body,
h3 {
    font-weight: 300
}

#logo a img {
    opacity: 0.6;
    &:hover {
        opacity: 1;
    }
}

#content p {
    font-size: 1.25em;
    margin-bottom: 0.5em;
}

#content p a:not(.sidebar-button) {
    padding: 0 2px;
    &:hover,
    &:focus {
        //color:  $content-link;
        color: white;
        background-color: lighten($content-link, 10%);
        //background-color: lighten($main-text-color, 10%);
    }
    &:before,
    &:after {
        content: "";
        padding: 0 -2px;
    }
    &:visited {
        color: $dark-text-color;
    }
}

ul.circle li ul,
ul.disc li ul,
ul.square li ul {
    list-style: inherit
}

.case-caption a,
.contact-email,
.label.services a,
.side-nav li a {
    -ms-transition: all .25s ease
}

div.panel h5,
h2,
h3,
h4 {
    font-family: inherit
}

h2 {
    font-weight: 400;
    font-size: 2em
}

#content h2 {
    margin-top: 1.5em;
}

h3 {
    font-size: 1.5em
}

div.panel h4,
.overview-main h4 {
    font-weight: 400;
    font-size: 1.5em
}

div.panel h5,
.overview-main h5 {
    font-weight: 400;
    font-size: 1.25em;
    font-family: Lato, sans-serif;
    text-transform: none;
}

.overview-main h5 {
    font-weight: 400;
    font-size: 1.25em;
    font-family: Lato, sans-serif;
    text-transform: uppercase;
    margin-top: 1.5em;
    color: $main-text-color;
}

.side-nav li,
figcaption,
p {
    font-family: Lato, sans-serif
}

#topbar {
    margin: 0 0 30px
}

#heading {
    margin: 2px 0
}

h1#logo {
    margin: 0
}

div.imagebox div.orbit {
    margin-bottom: 0
}

figure {
    display: block;
    position: relative;
    overflow: hidden
}

.js #loader,
.loading,
figcaption {
    position: absolute
}

figcaption {
    font-weight: 400;
    font-size: .75em;
    background: #333;
    background: rgba(0, 0, 0, .75);
    color: #fff;
    padding: 20px;
    width: 100%;
    opacity: 0;
    left: 0;
    bottom: -30%;
    -webkit-transition: all .6s ease;
    -moz-transition: all .6s ease;
    -o-transition: all .6s ease
}

.case-caption a,
.contact-email,
.label.services a,
.side-nav li a {
    -o-transition: all .25s ease;
    -moz-transition: all .25s ease;
    -webkit-transition: all .25s ease
}

figure:hover figcaption {
    opacity: 1;
    bottom: 0
}

.project-thumb-grid .imagebox {
    margin-bottom: 1em;
}

.imagebox {
    display: block;
    margin: 0 0 2em;
    line-height: 0;
    -moz-box-shadow: -2px 3px 5px 0 #ccc;
    -webkit-box-shadow: -2px 3px 5px 0 #ccc;
    box-shadow: -2px 3px 5px 0 #ccc
}

.side-nav li,
p {
    line-height: 1.8em
}

.overview-main .imagebox {
    margin: 1.5em 0 2em
}

.overview-main h3 {
    margin-top: 1.5em;
    border-bottom: 1px solid #aaa;
    padding-bottom: .5em
}

.label.services a,
ul.services a {
    color: #999!important;
    border-bottom: dotted 1px #999;
}

