    margin-top: -1.75em;
}

.side-nav li,
p {
    font-size: 1.1em
}

p.intro,
p.tagline {
    margin: 1.5em 0 3px
}

#content p.case-caption a {
    font-weight: 400;
    color: $content-link;
    transition: all .25s ease &:hover {
        color: #fff;
        //color: $content-link;
    }
    &:visited {
        color: #222;
    }
    &:visited:hover {
        color: #222;
    }
}

a:hover {
    // color: #777;
    color: $content-link;
}

p {
    font-weight: 300
}

p.tagline {
    font-weight: 400
}

.side-nav li {
    color: #777;
    font-weight: 300;
    position: relative;
}

ul.side-nav li a {
    color: #777;
    font-weight: 400;
    //transition: all .25s ease;
    position: relative;
    display: inline-block;
}

.side-nav li a:hover,
ul.side-nav li.active a {
    color: #000;
    font-weight: 400 !important;
    transition: all .25s ease;
}

@media only screen and (min-width: 768px) {
    .side-nav li:not(.active) {
        a {
            &:after {
                content: "";
                margin: 0 auto;
                position: absolute;
                opacity: 0;
                bottom: 0.125em;
                left: 50%;
                right: 50%;
                transition: left 250ms ease-in-out, right 250ms ease-in-out;
                margin-top: 0.25em;
                border-bottom: 2px solid lighten($cjd-branding, 25%);
            }
            &:hover:after {
                left: 0;
                right: 0;
                opacity: 1;
            }
        }
    }
}

ul.side-nav {
