.label.services a:hover {
    color: #333!important;
    border-bottom: dotted 1px #333;
}

.contact-button {
    margin: 3em 0
}

.contact-email {
    color: #fff;
    font-size: 1em;
    font-weight: 400;
    padding: 1em 1.25em;
    background-color: #999;
    -border-radius: 9px;
    -moz-border-radius: 9px;
