// Tabs
.tabs {
    //background-color: $secondary-bg-color;
    border-bottom: none;
    height: auto;
    //text-align: center;
    width: 100%;
    margin: 1em auto;
}

.tabs li.tab-title {
    color: #b3b3b3;
    cursor: default;
    display: inline-block;
    float: none;
    //font-size: 1em;
    //height: 40px;
    //line-height: 40px;
    //padding: 0 9px 0 20px;
    //width: 220px;
    text-transform: uppercase;
    // background-color: darken($secondary-bg-color, 5%);
    //border-bottom: solid 1px #e6e6e6;
    //float: left;
    padding: 0;
    // margin: 1em 1em 2em;
    margin: 1.5em 1em 1.5em;
    position: relative;
}

.tabs li.tab-title a {
    color: #6f6f6f;
    //color: $light-text-color;
    background-color: darken($secondary-bg-color, 10%);
    //background-color: lighten($content-link, 10%);
    display: block;
    font-size: 1em;
    font-weight: bold;
    letter-spacing: 1px;
    height: auto;
    line-height: 40px;
    //padding: 0 2em;
    // &:hover {
    //     font-weight: bold;
    // }
    &:after {
        -webkit-transition: top 100ms ease-in-out;
        -moz-transition: top 100ms ease-in-out;
        -o-transition: top 100ms ease-in-out;
        transition: top 100ms ease-in-out;
        content: "";
        height: 0px;
        width: 0px;
        position: absolute;
        top: 65%;
        left: 44%;
        //left: calc(50% - #{$arrow-size});
        // border-left: $arrow-size solid transparent;
        // border-right: $arrow-size solid transparent;
        // border-top: $arrow-size solid #fff;
        border: $arrow-size solid transparent;
        border-top-color: darken($secondary-bg-color, 10%);
        //border-top-color: lighten($content-link, 10%);
        //z-index: 10;
    }
    &:hover {
        color: $dark-text-color;
    }
    &:hover:after {
        top: 100%
    }
}

.tabs li.tab-title a:focus {
    font-weight: bold;
    color: #999;
    //color: $main-bg-color;
}

.tabs li.tab-title.active {
    //background-color: $dark-bg-color;
    //border-bottom: solid 1px $main-bg-color;
    border-top: none;
    margin-top: 1em;
}

.tabs li.tab-title.active a {
    cursor: default;
    color: $light-text-color;
    background-color: $dark-bg-color;
    border-left: none;
    border-right: none;
    font-weight: bold;
    &:after {
        // content: "";
        // height: 0px;
        // width: 0px;
        // position: absolute;
        top: 100%;
        // left: calc(50% - #{$arrow-size});
        border-top-color: $dark-bg-color;
        // border-left: $arrow-size solid transparent;
        // border-right: $arrow-size solid transparent;
        // border-top: $arrow-size solid #fff;
        // z-index: 10;
    }
}

.tabs li.tab-title:first-child {
    margin-left: 0;
}

