        //font-size: 1.5em;;
    }
    .overview {
        max-width: 600px;
    }
    figcaption {
        font-weight: 400;
        font-size: .875em; //.75em
        background: rgba(0, 0, 0, 0.875);
        //background: $dark-brand-bg;
        //background: $dark-bg-color;
        color: #fff;
        padding: 20px;
        width: 100%;
        opacity: 0;
        left: 0;
        bottom: -30%;
        -webkit-transition: all .6s ease;
        -moz-transition: all .6s ease;
        -o-transition: all .6s ease;
    }
    #content .twelve.columns > h2,
